.App {
  text-align: center;
  padding: 2rem;
  gap: 1rem;
  box-shadow: 0 0 10px black;
  margin: 3rem 20rem;
  border-radius: 5px;

}

input {
  padding: .5rem;
  border-radius: 5px;
  border: 1px solid grey;
  margin-right: 1rem;
  margin-bottom: 1rem;
}

button {
  padding: .2rem;
  background-color: rgb(72, 72, 217);
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
}

.list-item {
  justify-content: space-between;
  display: flex;
  margin: 1rem 7rem;
  text-transform: capitalize;
  font-weight: 600;
}


.list-item li {
  list-style-type: none;
}

.dlt-btn {
  background-color: rgb(224, 35, 35);
}

.popup {
  font-size: 18px;
  color: darkblue;
  font-weight: 700;
}

@media screen and (max-width: 600px) {
  .App {
    all: unset;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 1rem;
  }

  .list-item {
    all: unset;
    display: flex;
    justify-content: space-between;
    padding: .8rem 2rem;
  }
}